<template>
  <div class="film-box" style="height:100%">
    <div class="content" :style="{ minHeight: curHeight+'px'}" style="padding-top: 1rem;">
      <div class="fixed-box">
      <!--导航-->
      <div class="hedeBox" style="position: fixed;left: 0;top: 0;width: 100%;z-index: 10;   ">
        <nav-head :title="title" ></nav-head>
      </div>
      <!--header 头部-->
      <div class="header">
        <!--第二栏-->
        <div class="second-tab">
<!--          tabbar区域 @click="onClick" @click="navClick(item.title)" -->
          <van-tabs @click="navClick" v-model="active" color="#06A9FF" title-active-color="rgba(255,255,255,0.7)" title-inactive-color="rgba(255,255,255,0.5)" background="#202641" :border="false" line-height="2">
            <van-tab :title="v.title" v-for="(v,i) in navList" :key="i">
              <div class="dividing-line"></div>
                  <div class="main">
                    <div v-for="(item,index) in orderList" :key="index" class="list-item">
                      <div class="list-nav-box">
                        <div class="list-nav-left">
                          <!-- <span class="left-icon">东</span> margin-left: 0.27rem; -->
                          <span style="font-size: 0.28rem">币种：{{item.currency_name}}</span>
                        </div>
                        <p style="font-size: 0.24rem;">{{format(item.create_time)}}</p>
                      </div>
                      <!--          单价栏-->
                      <div class="list-price-box">
                        <div class="list-price-left">
                          <span class="left-text jj"> 类&nbsp;&nbsp;&nbsp;&nbsp;型：</span>
                          <span style="margin-left: 0.2rem;">余额充值{{item.type}}</span>
                        </div>
                      </div>
                      <!--          数量栏-->
                      <div class="list-num-box">
                        <div class="list-num-left">
                          <span class="left-text">描述信息：</span>
                          <span style="margin-left: 0.3rem;"> {{item.info}}</span>
                        </div>
                      </div>
                      <!--          限额栏-->
                      <div class="list-limit-box">
                        <div class="list-limit-left">
                          <span class="left-text jj">金 额：</span>
                          <span style="margin-left: 0.18rem;">{{item.num}}</span>
                        </div>
                      </div>
                    </div>
                  </div>
            </van-tab>
          </van-tabs>
        </div>
        <!--      分割线-->
        <!-- <div class="dividing-line"></div> -->
      </div>
      </div>
      <!-- 固定区域************************ -->
      <!-- 占位区域 -->
      <div class="zhanwei"></div>
      <!--    主要区域  "向他购买"  -->

    </div>
  </div>
</template>

<script>
import Vue from 'vue'
import { bill } from '@/api/user'
import { objKeySort, sgin } from '@/utils/auth'
import NavHead from '@/components/head.vue'
import { Tab, Tabs } from 'vant'

Vue.use(Tab)
Vue.use(Tabs)
export default {
  components: { NavHead },
  data () {
    return {
      active: 0,
      navList: [
        //  type是string账单类型（充值，提现，划转，分红，投币，矿池，拍卖，新币，还币，法币，币币，合约）
        {
          title: '充值'
        },
        {
          title: '提现'
        },
        {
          title: '划转'
        },
        {
          title: '分红'
        },
        {
          title: '投币'
        },
        {
          title: '矿池'
        },
        {
          title: '拍卖'
        },
        {
          title: '新币'
        },
        {
          title: '还币'
        },
        {
          title: '法币'
        },
        {
          title: '币币'
        },
        {
          title: '合约'
        }
      ],
      parameter: {
        u_id: 1,
        type: '充值',
        page: '1',
        showpage: '40'
      },
      curHeight: '',
      title: '全部订单',
      orderList: []
    }
  },
  created () {
    console.log(this.$route.query.types)
    var types = this.$route.query.types
    // if (types !== undefined) {
    //   this.parameter.type = '币币'
    //   this.active = 10
    // }
    if (types === '币币') {
      this.parameter.type = '币币'
      this.active = 10
    }
    if (types === '提现') {
      this.parameter.type = '提现'
      this.active = 1
    }
    if (types === '充值') {
      this.parameter.type = '充值'
      this.active = 0
    }
    if (types === '合约') {
      this.parameter.type = '合约'
      this.active = 11
    }
    if (types === '法币') {
      this.parameter.type = '法币'
      this.active = 9
    }
    if (types === '分红') {
      this.parameter.type = '分红'
      this.active = 3
    }
    this.parameter.u_id = sessionStorage.getItem('u_id')
    var data = objKeySort(this.parameter)
    var token = sgin(data)
    this.parameter.token = token
    this.allList(this.parameter)
    this.beforeMount()
  },
  methods: {
    add0 (m) { return m < 10 ? '0' + m : m },
    format (isTime) {
      var times = isTime * 1000
      var time = new Date(times)
      var y = time.getFullYear()
      var m = time.getMonth() + 1
      var d = time.getDate()
      var h = time.getHours()
      var mm = time.getMinutes()
      var s = time.getSeconds()
      console.log(y + '-' + this.add0(m) + '-' + this.add0(d) + ' ' + this.add0(h) + ':' + this.add0(mm) + ':' + this.add0(s))
      return y + '-' + this.add0(m) + '-' + this.add0(d) + '  ' + this.add0(h) + ':' + this.add0(mm) + ':' + this.add0(s)
    },
    beforeMount (height) {
      var h = document.documentElement.clientHeight || document.body.clientHeight
      this.curHeight = h
      console.log(this.curHeight, 'this.curHeight')
    },
    navClick (index) {
      var that = this
      console.log(index, 'index', this.navList[index].title)
      that.active = index
      that.parameter.type = that.navList[index].title
      var data = objKeySort(that.parameter)
      var token = sgin(data)
      that.parameter.token = token
      that.allList(that.parameter)
    },
    allList (parameter) {
      var that = this
      bill(parameter).then(res => {
        console.log(res, that, '账单成功')
        if (res.code === 0) {
          this.orderList = res.data
          delete this.parameter.token
        }
      }).catch((err) => {
        console.log(err)
        delete this.parameter.token
      })
    }
  }
}
</script>
